  display: flex;
  flex-direction: column;
  width: 100%;
  pointer-events: auto;
  background-color: #fff;
  background-clip: padding-box;
  border-radius: 7px;
  outline: 0;
  max-height: 100%;
  overflow: hidden;
}

.modal-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1050;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
}

.btn-close {
  background: url("../images/icon-x.svg") no-repeat 0 0;
  background-size: contain;
  width: 20px;
  height: 20px;
  opacity: 1;
}
.btn-close.white {
  background-image: url("../images/icon-x-white.svg");
}
.btn-close.circle {
  display: block;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background: rgba(0, 0, 0, 0.5);
  position: absolute;
  right: 20px;
  top: 20px;
  z-index: 2;
}
.btn-close.circle .icon-x-white {
  width: 16px;
  height: 16px;
}
.modal-content > .btn-close {
  position: absolute;
  right: 20px;
  top: 20px;
  z-index: 2;
}

.btn-close-line {
  text-align: center;
  width: 100%;
  padding: 10px;
  position: absolute;
  left: 0;
  top: 0;
  line-height: 1;
  font-size: 0;
  z-index: 2;
}
.btn-close-line::before {
  content: "";
  width: 34px;
  height: 4px;
  background: rgba(0, 0, 0, 0.25);
  display: inline-block;
}
@media (min-width: 992px) {
  .btn-close-line {
    display: none;
  }
}

