.modal.slide-up {
  margin: 0;
  height: 100%;
  overflow: hidden;
  align-items: flex-end;
  left: 50%;
  transform: translateX(-50%);
  transition: transform 200ms cubic-bezier(0.4, 0, 0.6, 1) 0ms;
}
.modal.slide-up .modal-dialog {
  margin: 0;
  max-width: 100%;
  width: 100%;
  overflow: visible;
  align-items: flex-end;
  min-height: 100%;
}
.modal.slide-up .modal-content {
  background: transparent;
  transform: translateY(100%);
  transition: all 0.35s;
  border: 0;
  border-radius: 7px 7px 0 0;
  max-height: 100vh;
  overflow: auto;
}
.modal.slide-up .modal-body {
  border-radius: 7px 7px 0 0;
  background: #fff;
  flex: 0 0 auto;
  padding: 40px 20px 40px;
  padding-bottom: calc(constant(safe-area-inset-bottom) + 40px);
  padding-bottom: calc(env(safe-area-inset-bottom) + 40px);
}
.modal.slide-up .modal-footer {
  background: #fff;
}
.modal.slide-up .modal-overflow-y-auto {
  overflow-x: hidden;
  max-height: calc(100% - 40px);
  overflow: auto;
}
.modal.slide-up.fade {
  transition: all 0.225s;
}
.modal.slide-up.show .modal-content {
  opacity: 1;
  transform: translateY(0);
  transition: transform 225ms cubic-bezier(0, 0, 0.2, 1) 150ms;
}
@media (min-width: 992px) {
  .modal.slide-up {
    transition: none !important;
  }
  .modal.slide-up .modal-dialog {
    align-items: center;
    margin: 0 auto;
    max-width: 360px;
  }
  .modal.slide-up .modal-content {
    transition: none !important;
  }
  .modal.slide-up .modal-body {
    border-radius: 7px;
  }
}

