.nav-tabs {
  display: flex;
  flex-wrap: wrap;
  padding-left: 0;
  margin-bottom: 0;
  list-style: none;
  gap: 10px;
}
.nav-tabs::before {
  content: "";
  height: 200px;
  background: #fff;
  position: absolute;
  top: -200px;
  left: 0;
  right: 0;
  z-index: -1;
}
.nav-tabs .nav-item {
  flex: 1;
}
.nav-tabs .nav-link {
  color: #999;
  font-size: 14px;
  line-height: 1.1574;
  text-align: center;
  display: block;
  padding: 10px 0 15px;
  position: relative;
  letter-spacing: -0.02em;
}
.nav-tabs .nav-link.active {
  color: #000;
  font-weight: 700;
}
.nav-tabs .nav-link.active::before {
  content: "";
  height: 2px;
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  background: #000;
}
@media (min-width: 992px) {
  .nav-tabs::before {
    display: none;
  }
  .nav-tabs .nav-item {
    flex: 0 1 auto;
  }
  .nav-tabs .nav-link {
    padding-top: 15px;
    font-size: 16px;
  }
  .nav-tabs .nav-link.active::before {
    display: none;
  }
  .nav-tabs.justify-content-lg-end .nav-item {
    display: flex;
    align-items: center;
  }
  .nav-tabs.justify-content-lg-end .nav-item + .nav-item::before {
    content: "";
    display: block;
    width: 1px;
    height: 14px;
    background: rgba(0, 0, 0, 0.15);
    margin: 0 15px 0 5px;
  }
