@keyframes bounce {
  0% {
    transform: translate3d(0, 0, 0);
  }
  35% {
    transform: translate3d(40px, 12px, 0);
  }
  60% {
    transform: translate3d(35px, -10px, 0);
  }
  90% {
    transform: translate3d(-10px, 8px, 0);
  }
  100% {
    transform: translate3d(0, 0, 0);
  }
}
@keyframes bounce2 {
  0% {
    transform: translate3d(0, 0, 0);
  }
  35% {
    transform: translate3d(35px, -10px, 0);
  }
  60% {
    transform: translate3d(20px, 12px, 0);
  }
  90% {
    transform: translate3d(-15px, 17px, 0);
  }
  100% {
    transform: translate3d(0, 0, 0);
  }
}
@keyframes bounce3 {
  0% {
    transform: translate3d(0, 0, 0);
  }
  35% {
    transform: translate3d(10px, 15px, 0);
  }
  60% {
    transform: translate3d(-60px, -5px, 0);
  }
  90% {
    transform: translate3d(-40px, 20px, 0);
  }
  100% {
    transform: translate3d(0, 0, 0);
  }
}
